<template>
  <div id="app">
    <SignupHeader></SignupHeader>
    <SignupPut @userSignUp="userSign" @idnoneAlert="idnoneAlert" @pwFalseAlert="pwFalseAlert"></SignupPut>
  </div>
  <AlertModal :show="modalShow" header="알림창" :body="body" @close="changeShow"></AlertModal>
</template>

<script>
import SignupHeader from './components/signup/SignupHeader.vue';
import SignupPut from './components/signup/SignupPut.vue';
import AlertModal from './components/common/AlertModal.vue';
import axios from 'axios';

export default {
  data() {
    return {
      modalShow : false,
      body : ""
    }
  },
  components: {
    SignupHeader,
    SignupPut,
    AlertModal
  },
  methods: {
    userSign(id, pw, nm) {
      axios.post('/user/signup', {
        'id':id,
        'pw':pw,
        'nm':nm
      })
      .then(res => {
        if(res.status !== 200) {
          this.modalShow = true;
          this.body = "회원가입이 실패하였습니다.";
        }
      })
    },
    idnoneAlert(val) {
      this.modalShow = val;
      this.body = "아이디를 입력해주세요";
    },
    pwFalseAlert(val) {
      this.modalShow = val;
      this.body = "입력하신 비밀번호가 다릅니다. 다시 입력해주세요";
    },
    changeShow() {
      this.modalShow = false;
      this.body = "";
    }
  }
  
}
</script>

<style>
.subColor {color: #7BB3BC;}
.bgMainColor {background-color: #B2EBF4;}
* {  margin: 0; padding: 0;}
html {
  background-color: #B2EBF4;
  width: 100%;
  height: 100%;
  }
body { 
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  height: 80%;
}
body > div {
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
}
#app {
  flex-basis: 60%;
  padding: 1% 10%;
  background-color: #fff;
}
</style>
