<template>
  <div class="main">
    <div><input type="text" name="id" v-model="id" @keyup.tab="idnone" placeholder="학번을 입력해주세요"></div>
    <div><input type="password" name="pw" v-model="pw" placeholder="비밀번호를 입력해주세요"></div>
    <div><input type="password" name="repw" v-model="repw" placeholder="비밀번호를 다시 입력해주세요"></div>
    <div><input type="text" name="nm" v-model="nm" @keyup.tab="pwFalse" placeholder="이름을 입력해주세요"></div>
    <div class="subColor footerBtn">
      <div class="pointer" @click="userSign">JOIN</div>
      <div class="pointer" @click="goLogin">LOGIN</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
        return {
            id: '',
            pw: '',
            repw: '',
            nm: ''
        }
    },
  methods: {
    idnone() {
      if(this.id.trim() === '') {
        this.$emit('idnoneAlert', true);
        this.id = "";
      }
    },
    pwFalse() {
      if(this.pw !== this.repw) {
        this.$emit('pwFalseAlert', true);
        this.pw = "";
        this.repw = "";
      }
    },
    userSign() {
      if(this.id.trim() !== '' && this.pw.trim() !== '' && this.repw.trim() !== '' && this.nm.trim() !== '') {
        const id = this.id
        const pw = this.pw
        const nm = this.nm
        this.$emit('userSignUp', id, pw, nm);
      }
    }
  }
}
</script>

<style scoped>
  main {
    width: 100%;
    text-align: center;
    }
  input {width: 100%; height: 55px; margin-bottom: 35px; text-align: center;
    background-color: #B2EBF4;
    font-size: 1rem;
    border: none;}
  .footerBtn{
    text-align: center; display: flex;
    justify-content: space-evenly;
    font-size: 1.5rem;
    font-weight: bold;
  }
  .pointer { cursor: pointer;}
</style>